


import { ToyReact } from './ToyReact'

class MyComponent {
  render() {
    return <div>
      <span>你</span>
      <span>好</span>
    </div>
  }
  setAttribute(name, value) {
    this[name] = value
  }
  mountTo(parent) {
    let vdom = this.render()
    vdom.mountTo(parent)
  }
}


